
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0 0 60px 0;
}

h1[data-v-d8ec41bc], h2[data-v-d8ec41bc] {
  font-weight: normal;
}
ul[data-v-d8ec41bc] {
  list-style-type: none;
  padding: 0;
}
li[data-v-d8ec41bc] {
  display: inline-block;
  margin: 0 10px;
}
a[data-v-d8ec41bc] {
  color: #42b983;
}

.month {
    max-width:900px;
    margin: 0 auto;
    border-top:1px solid black;
}
.month-change {
    margin: 10px 0;
}
.week {
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    border-left:1px solid black;
}
.day {
    -webkit-box-flex:1;
        -ms-flex:1;
            flex:1;
    min-height:110px;
    border-right:1px solid black;
    border-bottom:1px solid black;
    cursor: pointer;
}
.thin {
    background-color: rgb(187, 184, 184);
    pointer-events: none
}
/* 今月出ないものの件数を非表示に */
.thin .noMonth{
    display: none;
}
.day-disp {
    font-weight: 600;
    font-size: 23px;
    margin: 5px 0 10px 0;
}
.overlay {
    z-index:1;
    position:fixed;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background-color:rgba(0,0,0,0.5);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.modal {
    z-index:2;
    width:40%;
    padding: 15px;
    background:#fff;
}
@media screen and (max-width: 896px) {
.modal {
    width:70%;
}
}

.close-modal-but {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.form-title{
    margin-top: 10px;
}
.form-button {
    margin-top: 10px;
}
.time-table {
    margin: 10px 0;
}
.time-schedule {
    overflow-y: scroll;
    max-height: 300px;
}
.time-box {
    border: 1px solid;
}
.n-time {
    text-align: left;
}
.v-minits {
    visibility:hidden;
}
.delete-button {
    cursor: pointer;
}
.delete-button:hover {
    font-weight: 500;
    text-decoration: underline;
}
@media screen and (max-width: 480px) {
.form-detail {
    display: inline-block;
    width: 80%;
}
}
